import { NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CoprateUserCrudComponent } from './coprate-user-crud/coprate-user-crud.component';
import { TABS_DEMO_ROUTES } from './coprate-user-crud/routes';

const routes : Routes = [
    {
        path : '',component : CoprateUserCrudComponent, children : TABS_DEMO_ROUTES
    }
    
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class CoprateUserRoutingModule{}