import { NgModule } from '@angular/core'; 
import { SharedModule } from 'app/shared/shared.module';
import { CoprateUserCrudComponent } from './coprate-user-crud/coprate-user-crud.component';
import { CreateComponent } from './create/create.component';
import { ListComponent } from './list/list.component';
import { CoprateUserRoutingModule } from './coprate-user-routing.module';
import { ActionDialogComponent } from './action-dialog/action-dialog.component';
import { FuseSharedModule  } from '@fuse/shared.module';
const COMPONENTS =[CoprateUserCrudComponent, CreateComponent, ListComponent];
const COMPONENTS_DYNAMIC =[ActionDialogComponent];

@NgModule({
    imports:[SharedModule, CoprateUserRoutingModule,FuseSharedModule],
    declarations : [COMPONENTS,COMPONENTS_DYNAMIC],
    entryComponents : [COMPONENTS_DYNAMIC]
})

export class CoprateUserModule{}