
import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ActionDialogComponent } from '../action-dialog/action-dialog.component';
import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { CorporateService } from 'app/shared/services/corporate/corporate.service';
import { ViewEncapsulation } from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import { MatTableFilter } from 'mat-table-filter';
import * as XLSX from 'xlsx';
import { jsPDF } from 'jspdf';
import 'jspdf-autotable';
export class CoprateUsers {
  name: string;
  coprate_code: string;
  cin_number: string;
  register_number: string;
  phone_number: string;
  date_of_Incopration: string;
  contract_end_date: string;
  product: string;

}

/**
 * @title Data table with sorting, pagination, and filtering.
 */
@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations: fuseAnimations

})
export class ListComponent implements OnInit {

  displayedColumns: string[] = ['name', 'coprate_code', 'cin_number', 'register_number', 'phone_number', 'date_of_Incopration',
    'contract_end_date', 'product', 'action'];
  head = [['Name','Coprate Code','CIN Number','Register Number','Phone Number','Date Of Incopration','Contract End Date','Product']];
  dataSource: MatTableDataSource<CoprateUsers>;

  coprateUsers: CoprateUsers[];
  filterEntity: CoprateUsers;
  filterType: MatTableFilter;
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  nameSearch = false;
  coprateCodeSearch = false;
  cinNumberSearch = false;
  registerNumberSearch = false;
  phoneNumberSearch = false;
  dateOfIncoprationSearch = false;
  contractEndDateSearch = false;
  productSearch = false;
  exportedData: any[];
  localStorage: any;
  constructor(private corporateService: CorporateService, public dialog: MatDialog,
    private snackBar: MatSnackBar) {
    // Create 100 users
    // const users = Array.from({length: 100}, (_, k) => createNewUser(k + 1));

    // Assign the data to the data source for the table to render
  }

  ngOnInit() {
    this.localStorage = localStorage.getItem('userDetails');
    this.getAllCoprateUsers();
    this.filterEntity = new CoprateUsers();
    this.filterType = MatTableFilter.ANYWHERE;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  getAllCoprateUsers() {
    this.corporateService.getAllCorporate().subscribe(
      (data: any) => {
        this.coprateUsers = data;
        this.dataSource = new MatTableDataSource(this.coprateUsers);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      }, (err) => {
      }
    )
  }

  delete(id) {

    // this.rfidService.delete(id).subscribe(
    //   (data:any)=>{

    //     this.getRFids();
    //     this.snackBar.open('Deleted successfully','Dismiss',{duration:2000});
    //   },(err)=>{

    //   }
    // )
  }


  openDialog(action, data): void {

    let params = {
      action: action,
      data: data
    }

    const dialogRef = this.dialog.open(ActionDialogComponent, {
      width: '800px',
      data: params,
      autoFocus: false,
      disableClose: true
    });

    dialogRef.afterClosed().subscribe(result => {

      // this.moderate(result);
      this.getAllCoprateUsers();
    });
  }
  changeVisibility(visibleOption) {

    if (visibleOption == 'nameSearch') {
      this.nameSearch = !this.nameSearch;
      this.filterEntity.name = '';
    }

    if (visibleOption == 'coprateCodeSearch') {
      this.coprateCodeSearch = !this.coprateCodeSearch;
      this.filterEntity.coprate_code = '';
    }

    if (visibleOption == 'cinNumberSearch') {
      this.cinNumberSearch = !this.cinNumberSearch;
      this.filterEntity.cin_number = '';
    }
    if (visibleOption == 'registerNumberSearch') {
      this.registerNumberSearch = !this.registerNumberSearch;
      this.filterEntity.register_number = '';
    }
    if (visibleOption == 'phoneNumberSearch') {
      this.phoneNumberSearch = !this.phoneNumberSearch;
      this.filterEntity.phone_number = '';
    }
    if (visibleOption == 'dateOfIncoprationSearch') {
      this.dateOfIncoprationSearch = !this.dateOfIncoprationSearch;
      this.filterEntity.date_of_Incopration = '';
    }
    if (visibleOption == 'contractEndDateSearch') {
      this.contractEndDateSearch = !this.contractEndDateSearch;
      this.filterEntity.contract_end_date = '';
    }
    if (visibleOption == 'productSearch') {
      this.productSearch = !this.productSearch;
      this.filterEntity.product = '';
    }
  }

  tableExport() {

    this.exportedData = [];
    for (let i = 0; i < this.dataSource.filteredData.length; i++) {
      var coprateUsersModel = this.dataSource.filteredData[i];
      //this.exportedData.push({name: `name${i}`, description: `description${i}`,
      //statusString: `statusString${i}`});
      var data = {
        'Name': coprateUsersModel.name, 'Coprate Code': coprateUsersModel.coprate_code, 'CIN Number': coprateUsersModel.cin_number,
        'Register Number': coprateUsersModel.register_number, 'Phone Number ': coprateUsersModel.phone_number, 'Date Of Incopration ': coprateUsersModel.date_of_Incopration,
        'Contract End Date ': coprateUsersModel.contract_end_date, 'Product ': coprateUsersModel.product
      };
      this.exportedData.push(data);
    }
    const workSheet = XLSX.utils.json_to_sheet(this.exportedData);
    const workBook: XLSX.WorkBook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(workBook, workSheet, 'Coprate Users');
    XLSX.writeFile(workBook, 'Corporate Users.xlsx');
    //XLSX.writeFileAsync('OCPP Logs.xlsx',workBook, this.cb)
    //this.preloader.close();
  }

  pdfExport() {
    var doc = new jsPDF({ // set needed dimensions for any elemen

    });
    this.exportedData = [];
    for (let i = 0; i < this.dataSource.filteredData.length; i++) {
      var chrger = this.dataSource.filteredData[i];
      //this.exportedData.push({name: `name${i}`, description: `description${i}`,
      //statusString: `statusString${i}`});
      var data = [chrger.name, chrger.coprate_code, chrger.cin_number, chrger.register_number, chrger.phone_number, chrger.date_of_Incopration, chrger.contract_end_date,
        chrger.product,];

      this.exportedData.push(data);
    }
    var img = new Image();
    var imgRight = new Image();
    var src = 'assets/images/logos/logo_sparkev_main.png';
    var srcSparkEV = 'assets/images/logos/logo_SparkEV_black.png';
    imgRight.src = srcSparkEV;
    var createdDate = new Date().toLocaleString(('en-GB'));
    img.src = src;
    doc.setFontSize(11);
    doc.setTextColor(100);
    doc.setFontSize(18);

    (doc as any).autoTable({
      head: this.head,
      body: this.exportedData,
      theme: 'grid',
      headStyles: { fontStyle: 'majalla', fillColor: [11, 148, 68] },
      styles: {
        cellPadding: 1,
        fontSize: 8,
      },

      margin: { top: 22 },
      didDrawCell: data => {
      },
      didDrawPage: data => {
        doc.addImage(img , 15, 7, 15, 6);
        doc.addImage(imgRight, 170, 7, 25, 6);

        doc.text('Corporate Users Report', doc.internal.pageSize.getWidth() / 2, 11, { align: 'center' });

        doc.setTextColor(211, 211, 211);
        doc.setFontSize(60);
        doc.setGState(doc.GState({ opacity: 0.6 }));
        doc.text('Confidential/Not for Public use', 25, 285, null, 60,);
        doc.setGState(doc.GState({ opacity: 1 }));
        doc.setTextColor(100);
        doc.setFontSize(9);
        doc.text('Date:' + createdDate, 15, 18);

        doc.text('Generated By:' + this.localStorage.f_Name, 195, 18, { align: 'right' });
        doc = this.addWaterMark(doc);
      }
    })

    // below line for Open PDF document in new tab
    doc.output('dataurlnewwindow')
    // below line for Download PDF document  
    doc.save('Corporate Users  report.pdf');

  }
  addWaterMark(doc) {
    var newpage = doc.getNumberOfPages();
    const i = doc.internal.getNumberOfPages();
    doc.setTextColor(100);
    doc.setFontSize(10);
    //Print Page 1 of 4 for example

    doc.text('Page ' + String(i) + ' of ' + String(newpage), 210 - 20, 297 - 10, null, null, "right")
    return doc;
  }

}

