import { Component, OnInit } from '@angular/core';
import { Validators, FormBuilder, FormGroup,FormControl,ValidatorFn,AbstractControl } from '@angular/forms';
import { MasterService } from 'app/shared/services/master/master.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { UserService } from 'app/shared/services/user/user.service';
import { DatePipe } from '@angular/common';
import {Observable} from 'rxjs';
import {map, startWith} from 'rxjs/operators';
import {CountriesService} from 'app/shared/services/countries.service';
import {ViewEncapsulation} from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
function autocompleteObjectValidator(): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } | null => {
    if (typeof control.value === 'string') {
      return { 'invalidAutocompleteObject': { value: control.value } }
    }
    return null  /* valid option selected */
  }
} 
@Component({
  selector: 'app-create',
  templateUrl: './create.component.html',
  styleUrls: ['./create.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations   : fuseAnimations,
  providers:[DatePipe]
})
export class CreateComponent implements OnInit {
 
  isPasswordFieldType: boolean;
  isPasswordConfirmFieldType: boolean;
  form: FormGroup;
  brands:any;
  models:any;
  chargerTypes:any;
  vehicleTypes:any;
  localStorage : any;
  role_types:any;
  cpo_types:any;
  filteredOptionsRole: Observable<any[]>;
  filteredOptionsCpo: Observable<any[]>;
  filteredOptionsClient: Observable<any[]>;
  filteredOptionCountry: Observable<any[]>;
  filteredOptionState: Observable<any[]>;
  filteredOptionCity: Observable<any[]>;
  countries:any;
  stateName:any;
  cities:any;
  states:any;
  pin_code_id:any;
  country_id:any;
  state_id:any;
  constructor(private fb: FormBuilder, private masterService : MasterService,
    private userService : UserService,private snackBar: MatSnackBar,
    private router : Router,private datePipe: DatePipe,public countryService: CountriesService) {
     
    
  }

  ngOnInit() {
    this.initialize();
   //this.getCountry();
   this.getAllCountry();
   this.getAllState();
   //this.getAllCity();
  }

  initialize(){

    this.localStorage = JSON.parse( localStorage.getItem('userDetails'));

    this.form = this.fb.group({
      name:['',[Validators.required,Validators.pattern(/^[a-zA-Z0-9 ]*$/)]],
      coprate_code:['',[Validators.required,Validators.pattern(/^[a-zA-Z0-9 ]*$/)]],
      email:['',[Validators.required,Validators.pattern(/^[a-zA-Z0-9 ]*$/)]],
      register_number:['',[Validators.required,Validators.pattern(/^[a-zA-Z0-9 ]*$/)]],
      phone_number:['',[Validators.required,Validators.min(10000000000), Validators.max(999999999999)]],
      cin_number:['',[Validators.required,Validators.pattern(/^[a-zA-Z0-9 ]*$/)]],
      date_of_incorporation:['',[Validators.required]],
      contract_period_from:['',[Validators.required]],
      contract_period_to:['',[Validators.required]],
      gstin:['',[Validators.required,Validators.pattern(/^[a-zA-Z0-9 ]*$/)]],
      addressline1: ['', [Validators.required]],
      addressline2: ['', [Validators.required]],
      pincode: ['', [Validators.required]],
      landmark: ['', [Validators.pattern(/^[a-zA-Z0-9]*$/)]],
      country: ['', [autocompleteObjectValidator(),Validators.required]],
      state: ['', [autocompleteObjectValidator(),Validators.required]],
      city: ['', [Validators.required]],

     


    
     
    });
   //this.filteredOptions();
   
   this.getCpoTypes();
  }
  getAllCountry(){
    this.countryService.getAllCountry().subscribe(
      (data:any)=>{
        this.countries = data;
         this.filteredOptionCountry = this.form.controls.country.valueChanges
           .pipe(
             startWith(''),
             map(value => typeof value === 'string' ? value : value.name),
             map(name => name ? this._filterCountry(name) : this.countries)
           );
         },(err)=>{
           
         }
    )
  }
  getAllState(){
    this.countryService.getAllState().subscribe(
      (data:any)=>{
        this.states = data;
         this.filteredOptionState = this.form.controls.state.valueChanges
           .pipe(
             startWith(''),
             map(value => typeof value === 'string' ? value : value.name),
             map(name => name ? this._filterState(name) : this.states)
           );
         },(err)=>{
           
         }
    )
  }
  
  getAllCity(){
    this.countryService.getAllCities().subscribe(
      (data:any)=>{
        this.cities = data;
         this.filteredOptionCity = this.form.controls.city.valueChanges
           .pipe(
             startWith(''),
             map(value => typeof value === 'string' ? value : value.name),
             map(name => name ? this._filterCity(name) : this.cities)
           );
         },(err)=>{
           
         }
    )
  }
  
  getCpoTypes(){
    
    this.cpo_types = this.userService.getCPOTypes();
    this.filteredOptionsCpo = this.form.controls.cpoType.valueChanges
    .pipe(
      startWith(''),
      map(value => typeof value === 'string' ? value : value.name),
      map(name => name ? this._filterCpoType(name) : this.cpo_types)
    );
   
  }
  displayCpo(item: any): string {
    return item && item.name ? item.name : '';
  }
  private _filterCpoType(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.cpo_types.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  confirmValidator = (control: FormControl): { [s: string]: boolean } => {
    if (!control.value) {
      return { error: true, required: true };
    } else if (control.value !== this.form.controls.password.value) {
      return { error: true, confirm: true };
    }
  }
  togglePasswordFieldType(){
    this.isPasswordFieldType = !this.isPasswordFieldType;
  }
  togglePasswordConfirmFieldType(){
    this.isPasswordConfirmFieldType = !this.isPasswordConfirmFieldType;
  }
  onSubmit(){
    if (this.form.invalid){
      return;
    }else{
      let formValues = this.form.value;
      ////
  
      
      let params = {
        name :formValues.rf_name, 
        description :formValues.rfid_description, 
        rfid_no :formValues.rfid_number,
        expiry_date :this.datePipe.transform(formValues.rfid_expDate,"yyyy-MM-dd") ,
        status :'Y' ,
        created_date: this.datePipe.transform(formValues.rfid_expDate,"yyyy-MM-dd") ,
        created_by : this.localStorage.userId 
      }
  
      
      this.userService.create(params).subscribe(
        (data:any )=>{
          
        this.snackBar.open('Rfid created!', 'Dismiss', { duration: 5000 });
        this.router.navigate(['/rfidManagement/crud/list']);
  
        },
        (err)=>{
          this.snackBar.open('Please try again', 'Dismiss', { duration: 5000 });
        }
      )
    }
   

  }

  getErrorMessage(form: FormGroup) {
    return form.get('email').hasError('required')
      ? 'You must enter a value'
      : form.get('email').hasError('email')
      ? 'Not a valid email'
      : '';
  }
  getStateCountry(){
    
    this.pin_code_id = this.form.value.pincode;
  
  //;
  this.countryService.getCountryStateByPIN(this.pin_code_id ).subscribe(
    (data:any)=>{
      //this.form.reset();
      if(data != undefined && data != null && data.length > 0){
        this.state_id = data[0].state_id;
        //;
        this.countryService.getCountryByState(this.state_id ).subscribe(
          (countrydata:any)=>{
            //this.form.reset();
            this.countries = countrydata;
            const toSelect = this.countries.find(c => c.id == data[0].country_id);
            this.form.get('country').setValue(toSelect);
            
            this.filteredOptionCountry = this.form.controls.country.valueChanges
            .pipe(
              startWith(''),
              map(value => typeof value === 'string' ? value : value.name),
              map(name => name ? this._filterCountry(name) : this.countries)
            );
            //; 
            this.country_id = data[0].country_id;
            this.countryService.getStateByCountry(this.country_id ).subscribe(
              (statedata:any)=>{
                //this.form.reset();
                this.states = statedata;
                const toSelect = this.states.find(c => c.id == data[0].state_id);
                this.form.get('state').setValue(toSelect);
                
                this.filteredOptionState = this.form.controls.state.valueChanges
                .pipe(
                  startWith(''),
                  map(value => typeof value === 'string' ? value : value.name),
                  map(name => name ? this._filterState(name) : this.states)
                );
                
              },(err)=>{
                
              }
            )
            //; 
            this.state_id = data[0].state_id;
            this.countryService.getCityByState(this.state_id ).subscribe(
              (Citydata:any)=>{
                //this.form.reset();
                this.cities = Citydata;
                //const toSelect = this.states.find(c => c.id == data[0].state_id);
               // this.form.get('state').setValue(toSelect);
                
                this.filteredOptionCity = this.form.controls.city.valueChanges
                .pipe(
                  startWith(''),
                  map(value => typeof value === 'string' ? value : value.name),
                  map(name => name ? this._filterCity(name) : this.cities)
                );
                
              },(err)=>{
                
              }
            )
          },(err)=>{
            
          }
        )
      }else{

      }
     
      
    },(err)=>{
      
    }
  )
}
onCountryChange(event){
  this.countries = [];
  this.getAllCountry();
  this.getStatesByCountry(event);
  
 
  //this.form.get('pincode').setValue( "");
  this.form.get('state').setValue( "");
  this.form.get('city').setValue( "");
}
getStatesByCountry(state){
   this.state_id = state.id;
   this.countryService.getStateByCountry(this.state_id ).subscribe(
    (statedata:any)=>{
      //this.form.reset();
      this.states = statedata;
     // const toSelect = this.states.find(c => c.id == data[0].state_id);
     // this.form.get('state').setValue(toSelect);
      
      this.filteredOptionState = this.form.controls.state.valueChanges
      .pipe(
        startWith(''),
        map(value => typeof value === 'string' ? value : value.name),
        map(name => name ? this._filterState(name) : this.states)
      );
      
    },(err)=>{
      
    }
  )
    
    
  
}
onStateChange(event){
  this.getCitiesByStates(event);
  this.getCountryByState(event);
  this.form.get('city').setValue( "");
 // this.form.get('pincode').setValue( "");
}
onCityChange(event){
  
  //this.form.get('pincode').setValue( "");
}
getCitiesByStates(stateId){
  this.state_id = stateId.id;
   this.countryService.getCityByState(this.state_id ).subscribe(
    (citiesdata:any)=>{
      //this.form.reset();
      this.cities = citiesdata;
     // const toSelect = this.states.find(c => c.id == data[0].state_id);
     // this.form.get('state').setValue(toSelect);
      
      this.filteredOptionCity = this.form.controls.city.valueChanges
      .pipe(
        startWith(''),
        map(value => typeof value === 'string' ? value : value.name),
        map(name => name ? this._filterCity(name) : this.cities)
      );
      
    },(err)=>{
      
    }
  )
}
getCountryByState(stateId){
  this.state_id = stateId.id;
  this.countryService.getCountryByState(this.state_id ).subscribe(
    (countrydata:any)=>{
      //this.form.reset();
      this.countries = countrydata;
      const toSelect = this.countries.find(c => c.id == countrydata[0].id);
      this.form.get('country').setValue(toSelect);
      
      this.filteredOptionCountry = this.form.controls.country.valueChanges
      .pipe(
        startWith(''),
        map(value => typeof value === 'string' ? value : value.name),
        map(name => name ? this._filterCountry(name) : this.countries)
      );
     
   },(err)=>{
     
   }
 )
}
displayCountry(item: any): string {
  return item && item.name ? item.name : '';
}
private _filterCountry(name: string): any[] {
  const filterValue = name.toLowerCase();
  return this.countries.filter(option => option.name.toLowerCase().includes(filterValue));
}
displayState(item: any): string {
  return item && item.name ? item.name : '';
}
private _filterState(name: string): any[] {
  const filterValue = name.toLowerCase();
  return this.states.filter(option => option.name.toLowerCase().includes(filterValue));
}

displayCity(item: any): string {
  return item && item.name ? item.name : '';
}
private _filterCity(name: string): any[] {
  const filterValue = name.toLowerCase();
  return this.cities.filter(option => option.name.toLowerCase().includes(filterValue));
}
}


