import { Injectable } from '@angular/core';
import {environment} from '../../../../environments/environment';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class CorporateService {
  apiUrl2 = `${environment.API_URL2}/chargingStation` ;
  apiUrl3 = `${environment.API_URL3}` ;
  apiUrl4 = `${environment.API_URL2}` ;
  constructor(private http : HttpClient) { }

  create(params){
    return this.http.post(`${this.apiUrl2}/create/`,params);
  }
  update(params){
    return this.http.post(`${this.apiUrl2}/update/`,params);
  }
  getAllCorporate(){
    return this.http.get(`${this.apiUrl2}/getAllCorporate`);
  }
 
}
